import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

type Banner = {
  id: string;
  imageUrl: string;
  link: string;
  title: string;
  subtitle?: string;
};

const liveBanners: Banner[] = [
  {
    id: '1',
    imageUrl: 'https://images.unsplash.com/photo-1514525253161-7a46d19cd819?w=2000&h=700&fit=crop',
    link: '/live/concert-2024',
    title: 'Live Music Festival 2024',
    subtitle: 'Join us for an incredible night of live performances',
  },
  {
    id: '2',
    imageUrl: 'https://images.unsplash.com/photo-1578022761797-b8636ac1773c?w=2000&h=700&fit=crop',
    link: '/live/championship-boxing',
    title: 'Championship Boxing Night',
    subtitle: 'Watch the heavyweight championship live from Las Vegas',
  },
  {
    id: '3',
    imageUrl: 'https://images.unsplash.com/photo-1492684223066-81342ee5ff30?w=2000&h=700&fit=crop',
    link: '/live/comedy-special',
    title: 'Comedy Night Special',
    subtitle: 'An evening of non-stop laughter with top comedians',
  },
];

export function LiveBanner() {
  const [currentBanner, setCurrentBanner] = useState(0);

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentBanner((prev) => (prev + 1) % liveBanners.length);
    }, 5000);
    return () => clearInterval(timer);
  }, []);

  return (
    <div className="relative w-full h-[280px] mt-8 bg-white">
      <div className="relative w-full h-full flex items-center justify-center overflow-hidden">
        {/* Main Banner */}
        <div className="w-full h-full rounded-[2rem] overflow-hidden shadow-xl relative z-20">
          {liveBanners.map((banner, index) => (
            <Link
              key={banner.id}
              to={banner.link}
              className="absolute inset-0 transition-all duration-800"
              style={{
                opacity: index === currentBanner ? 1 : 0,
              }}
            >
              <img
                src={banner.imageUrl}
                alt={banner.title}
                className="w-full h-full object-cover"
              />
              <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-black/40 to-transparent">
                <div className="absolute bottom-0 left-0 right-0 p-8">
                  <h2 className="text-4xl font-light tracking-wide text-white mb-2">
                    {banner.title}
                  </h2>
                  {banner.subtitle && (
                    <p className="text-lg text-gray-200">{banner.subtitle}</p>
                  )}
                </div>
              </div>
            </Link>
          ))}
        </div>

        {/* Dots Navigation */}
        <div className="absolute bottom-4 left-1/2 -translate-x-1/2 flex gap-2 z-30">
          {liveBanners.map((_, index) => (
            <button
              key={index}
              onClick={() => setCurrentBanner(index)}
              className={`w-2 h-2 rounded-full transition-all duration-300 ${
                index === currentBanner ? 'bg-white w-6' : 'bg-white/50'
              }`}
              aria-label={`Go to banner ${index + 1}`}
            />
          ))}
        </div>
      </div>
    </div>
  );
}